import logo from './logo.svg';
import './App.css';
import gsap from 'gsap'
import {useEffect,useRef} from 'react'
import HeaderComp from './components/HeaderComp'
import InfoComp from './components/infoComp'

import { ScrollTrigger } from 'gsap/ScrollTrigger';



function App() {
  
gsap.registerPlugin(ScrollTrigger);
  
  useEffect(()=>{
    const tl2=gsap.timeline();

    gsap.to("body",0,{css:{visibility:"visible"}});

    tl2.from(".heads",1.8 ,{
            autoAlpha: 0,
            y:100,
              
            ease: "power4.out",
            delay: 0.3,
           
            stagger:{
              amount:0.4
            }
        })
    .to(".headw1",1.6,{
          height:0,
          ease:"expo.inOut",
          stagger:0.4
        })
        .to(".heads",0,{
            css:{color:"black"},
            delay:-1.55
        })
    .to(".hw1",1.6,{
            width:0,
            ease:"expo.inOut",
            stagger:0.4,
            delay:-0.8
          })
    .to(".herowrapper",0,{css:{display:"none"}})
    .to(".headerwrapper",0,{css:{display:"none"}})
    .from(".imgholder img",1.6,{
      scale:1.4,
      delay:-2,
      stagger:{amount:0.4}
    })
       
       gsap.from(".infoh1",1.6 ,{
      scrollTrigger:{
        trigger:'.infoh1',
        toggleActions:"restart none none none"
        
      } ,
      y: 50,
      stagger:{amount:0.4},
      autoAlpha: 0
    });  


        gsap.from(".infoh2",1.6 ,{
      scrollTrigger:{
        trigger:'.infoh2',
        toggleActions:"restart none none none"
        
      } ,
      y: 50,
       stagger:{amount:0.4},
      autoAlpha: 0
    });

         gsap.from(".infoh3",1.6 ,{
      scrollTrigger:{
        trigger:'.infoh3',
        toggleActions:"restart none none none"
        
      } ,
      y: 50,
       stagger:{amount:0.4},
      autoAlpha: 0
    });
       

gsap.utils.toArray(".panel").forEach((panel,i)=>{
  ScrollTrigger.create({
    trigger:panel,
    start:"top top",
    pin:true,
    pinSpacing:false
  });
});
  },[])

  return (
    <>
    <HeaderComp/>
    <InfoComp/>
    </>
  );
}

export default App;
